import { useEffect, useState } from "react";
import QLoader from "../../../../Loader/QLoader";
import { useTranslation } from "react-i18next";
import { CSVLink } from "react-csv";
import { useExportStaffSubjectAttendanceExcel } from "../DepartmentApi/depart-api";
import CreateButton from "../../../../optimized/Ui/Button/CreateButton";
const ExportStaffSubjectAttendance = ({ instituteId }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [downloadLink, setDownloadLink] = useState(false);
  const [exportData, setExportData] = useState([]);

  const [exportStaffSubjectAttendanceExcel] =
    useExportStaffSubjectAttendanceExcel();

  useEffect(() => {
    if (downloadLink) {
      let downlabel = document.getElementById("staff_attendance_download_file");
      downlabel.click();
      setDownloadLink(false);
      setExportData([]);
      setDisabled((pre) => !pre);
    }
  }, [downloadLink]);
  const generateMonthWiseExcel = (list) => {
    const all_staff = list?.map((stu) => {
      let obj = {
        "Index No": stu?.staffROLLNO ?? "N/A",
        "Employee Code": stu?.staff_emp_code ?? "N/A",
        Name:
          stu?.staffFirstName +
          " " +
          stu?.staffMiddleName +
          " " +
          stu?.staffLastName,
        ...stu?.dt_obj,
      };
      return obj;
    });
    return all_staff;
  };

  const onExportData = () => {
    if (instituteId) {
      setDisabled((pre) => !pre);
      exportStaffSubjectAttendanceExcel({
        id: instituteId,
      })
        .then((res) => {
          setExportData(() => generateMonthWiseExcel(res.data?.all_staff));
          setDownloadLink(true);
        })
        .catch(() => {});
    }
  };

  return (
    <>
      <CreateButton
        label={t("staff_status")}
        onAction={onExportData}
        customStyle={{
          margin: "0",
          padding: "0.3rem 1rem",
        }}
      />
      <CSVLink
        data={exportData}
        filename={`staff-attendace-mark`}
        id="staff_attendance_download_file"
      />
      {disabled && <QLoader />}
    </>
  );
};

export default ExportStaffSubjectAttendance;
