import { useEffect, useRef, useState } from "react";
import { useTranslation } from "react-i18next";
import Modal from "../../../../../optimized/Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../../optimized/Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../../optimized/Ui/Border/BorderBottom";
import CreateButton from "../../../../../optimized/Ui/Button/CreateButton";
import { assestsUserFinanceUrl } from "../../../../../optimized/Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../../../optimized/Loader/QvipleLoader";
import style from "../../../../../optimized/UserStaffMember/FinanceManager/Income/Income.module.css";
import StaffResuffleListCard from "./StaffResuffleListCard";
import { useGetInstituteSuffleStaffList } from "../../../../../hooks/institute/institute-staff-student-api";
import { useInstituteStaffShuffle } from "../../../../../optimized/Hooks/ApiHooks/FinanceManager/finance-manager-api";
import Loading from "../../../../../optimized/Loader/Loading";
const StaffResuffleList = ({ onClose, instituteId, onRefetch }) => {
  const { t } = useTranslation();
  const [allStaff, setAllStaff] = useState([]);
  const [disabled, setDisabled] = useState(false);
  const [showingDataLoading, setShowingDataLoading] = useState(true);
  const dragItem = useRef();
  const dragOverItem = useRef();
  const [instituteStaffShuffle] = useInstituteStaffShuffle();

  const { getInstituteSuffleStaffList, getInstituteSuffleStaffListRefetch } =
    useGetInstituteSuffleStaffList({
      data: {
        id: instituteId,
      },
      skip: !instituteId,
    });

  useEffect(() => {
    if (instituteId) {
      getInstituteSuffleStaffListRefetch();
      setShowingDataLoading(true);
    }
  }, [instituteId, getInstituteSuffleStaffListRefetch]);

  useEffect(() => {
    if (getInstituteSuffleStaffList?.all_staff?.length) {
      setAllStaff(getInstituteSuffleStaffList?.all_staff);
      setShowingDataLoading(false);
    }
  }, [getInstituteSuffleStaffList?.all_staff?.length]);

  const onDragEventStart = (e, position) => {
    dragItem.current = position;
  };

  const onDragEventEnter = (e, position) => {
    dragOverItem.current = position;
  };
  const onDropItem = (e) => {
    const studentCopy = [...allStaff];
    const dragItemContentCls = studentCopy[dragItem.current];
    studentCopy.splice(dragItem.current, 1);
    studentCopy.splice(dragOverItem.current, 0, dragItemContentCls);

    dragItem.current = null;
    dragOverItem.current = null;
    setAllStaff(studentCopy);
  };
  const onSaveResuffle = () => {
    if (instituteId) {
      setDisabled((pre) => !pre);
      let arr = [];
      for (let ids of allStaff) {
        arr.push(ids?._id);
      }
      instituteStaffShuffle({
        shuffle: {
          shuffle_arr: arr,
          id: instituteId,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <>
      <Modal onClose={onClose}>
        <div
          className={style.modal_container}
          style={{
            maxWidth: "47rem",
          }}
        >
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("staff_list")}</h6>
              </div>
              <div
                className={style.modal_container_outer_header}
                style={{
                  gap: "1rem",
                }}
              >
                <CreateButton
                  label="save"
                  customStyle={{
                    margin: "0",
                    padding: "0.2rem 1rem",
                    fontSize: "13px",
                  }}
                  onAction={onSaveResuffle}
                />
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            {allStaff?.map((row, index) => (
              <div
                key={row?._id}
                draggable
                onDragStart={(e) => onDragEventStart(e, index)}
                onDragEnter={(e) => onDragEventEnter(e, index)}
                onDragEnd={onDropItem}
              >
                <StaffResuffleListCard row={row} />
                <BorderBottom />
              </div>
            ))}
            {showingDataLoading && <Loading />}
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default StaffResuffleList;
